$font_content: 17px;
$font_title: 45px;
@font-face {
    font-family: 'font';
    src: url("./fonts/OscineTrial-Light/OscineTrial-Light.eot?#iefix") format("embedded-opentype"), url("./fonts/OscineTrial-Light/OscineTrial-Light.woff") format("woff"), url("./fonts/OscineTrial-Light/OscineTrial-Light.ttf") format("truetype"), url("./fonts/OscineTrial-Light/OscineTrial-Light.svg#OscineTrial-Light") format("svg");
    font-weight: normal;
    font-style: normal;
}

*{
  margin: 0;
  padding: 0;
}
body, html {
    box-sizing: border-box;
    font-size: $font_content;
    font-family: 'font';
    overflow-x: hidden; 
    background: #000000;
}

.plr-md-0 {
  padding: 0;
}

.mlr-md-0 {
  margin: 0;
}

svg {
  width: 100%;
}

img {
  object-fit: cover;
}

ul li{
  list-style: none;
}


/* start menu */
.background_black_load{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1000;
  background: #000000;
  opacity: 1;
  visibility: visible;
}




.background_load{
  section{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10000;
    transform-origin: 100% 100%;
    transform: scaleX(0);
    &:nth-child(1){
      background: rgba(255,255,62,1);
    }
    &:nth-child(2){
      background: rgba(255,255,255,1);
    }
    &:nth-child(3){
      background: rgba(0,0,0,1);
    }
  }
}

.logo{
  position: fixed;
  top: 15px;
  left: 15px;
  width: 35px;
  z-index: 999;
}


#Layer_logo .cls-1 {
    fill: none;
    stroke-dasharray: 1800;
    stroke-width: 3;
    opacity: 1;
}
#Layer_logo:hover path{
    stroke-dasharray: 1800;
    stroke: #ffffff;
    opacity: 1;
    animation: animateLoad 1s cubic-bezier(0,0.23,1,.1) forwards;
}
@keyframes animateLoad{
    0%{
        opacity: 0;
        fill:  none;
        stroke-dashoffset: 1800; 
    }
    10%{
        opacity: 1;
        fill: none;
        stroke-dashoffset: 1800;
    }
    90%{
        fill: #ffffff;
    }
    100%{
        opacity: 1;
        fill: #ffffff;
        stroke-dashoffset: 0;
    }
}



.button_menu{
  position: fixed;
  top: 15px;
  right: 10px;
  width: 39px;
  padding: 2px;
  cursor: pointer;
  z-index: 999;
    article{
      width: 100%;
      height: 2px;
      background: #ffffff;
      margin-bottom: 6px;
      transition: 0.5s ease;
    }
  &:hover article:nth-child(2){
    transform: translateX(10px);
  }
}

.show_menu{
  .button_menu{
    article{
      &:nth-child(1){
        transform: translate(0, 15px) rotate(-405deg);
      }
      &:nth-child(2){
        transform: translateX(100px);
      }
      &:nth-child(3){
        transform: translate(0,0) rotate(45deg);
      }
    }
  }
  .bg_black_menu,
  .menu{
    opacity: 1;
    visibility: visible;
  }
}
.bg_black_menu{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #000000;
  z-index: 500;
  opacity: 0;
  visibility: hidden;
  transition: 1s ease;
}
.menu{
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 888;
  color: #ffffff;
  padding-left: 5%;
  opacity: 0;
  visibility: hidden;
    .this_menu{
      position: relative;
      padding-bottom: 20px;
      article{
        position: relative;
        font-size: 40px;
        margin-top: 7%;
        margin-bottom: 5%;
        z-index: 2;
      }
      ul{
        display: flex;
        justify-content: space-around;
        li{
          position: relative;
        }
          a{
            font-size: 25px;
            color: #ffffff;
            text-decoration: none;
            position: relative;
            padding: 0 30px;
            z-index: 2;
             &::before{
              content: "";
              position: absolute;
              top: 50%;
              left: 0;
              width: 100%;
              height: 1px;
              background: #ffffff;
              transform-origin: 0% 0%;
              transform: scaleX(0);
              transition: 0.5s ease;
             }
             &:hover::before{
              transform: scaleX(1);
             }
          }
      }
      > div{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 40%;
        height: 1px;
        background: #ffffff;
      }
    }
    .adress{
      article{
        font-size: 40px;
        margin-top: 5%;
        margin-bottom: 5%;
      }
      div{
        padding-left: 7%;
        section{
          margin-bottom: 1rem;
        }
      }
    }

}
.img_menu{
  position: fixed;
  width: 40%;
  height: 100vh;
  transform-origin: 0% 0%;
  transform: scaleX(1);
  z-index: 888;
  overflow: hidden;
  filter: invert(5%);
   img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      &:nth-child(1){
        position: relative;
        opacity: 0;
      }
      &:nth-child(2){
        opacity: 1; 
      }
      &:nth-child(3){
        opacity: 0;
      }
   }
}

.link_share{
  position: fixed;
  bottom: 2%;
  left: 15px;
  z-index: 888;
    ul{
      display: flex;
      justify-content: space-around;
       li{
        margin-right: 15px;
        a{
          svg{
            width: 30px;
          }
          #Layer_fb:hover .cls-1,
          #Layer_fb:hover .cls-2{
            fill: #4834d4;
          }
          #Layer_twiwer:hover .cls-1,
          #Layer_twiwer:hover .cls-2{
            fill: #7ed6df;
          }
          #Layer_itr:hover .cls-1{
            fill: #be2edd;
          }
          #Layer_phone:hover .cls-1,
          #Layer_phone:hover .cls-2{
            fill: #eb4d4b;
          }
        }
       }
    }
}



@media (max-width: 768px){

  .menu{
      .this_menu{
        padding-bottom: 20px;
        article{
          font-size: 30px;
          margin-top: 25%;
          margin-bottom: 5%;
          color: #ffff3e;
        }
        ul{
            a{
              font-size: 20px;
              padding: 0 10px;
               &:hover::before{
                transform: scaleX(0);
               }
            }
        }
      }
      .adress{
        margin-top: 40px;
        article{
          font-size: 30px;
          color: #ffff3e;
        }
        div{
          padding-left: 4%;
          section{
            font-size: 12px;
            margin-bottom: 1rem;
          }
        }
      }
  
  }

  .img_menu{
    opacity: 0!important;
    visibility: hidden!important;
  }
  
  
}


