  }
  a{
    text-decoration: none;
    color: #ffffff;
      &:hover .item_content_listmenu::before{
        transform: scaleX(1);
      }
  }
}
.item_content_listmenu{
  margin-bottom: 20px;
  position: relative;
  .once_item_content_listmenu {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 20px;
    .item_content_listmenu_title {
      font-family: 'Playfair Display', serif;
      flex-grow: 1;
      font-size: 18px;
      color: rgba(255,255,62,1);
      // flex: 2;
    }
    .item_content_listmenu_space {
      flex-basis: 1;
      overflow: hidden;
      flex: 20;
      flex-shrink: 4;
      align-self: felx-end;
      text-align: right;
      margin-right: 10px;
      border-bottom: 1px dotted #ffffff;
      font-size: 0px;
      line-height: 19px;
    }
    .item_content_listmenu_price {
      font-family: 'Playfair Display', serif;
      // min-width: 60px;
      align-self: felx-end;
      display: flex;
      svg#Layer_eye{
        width: 30px;
         .cls-1{
           fill: #ffffff;
         }
      }
