        margin-top: 7px;
        font-size: 10px;
      }
    }
  }
  &::before{
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: rgba(255,255,62,1);
    transform-origin: 0% 0%;
    transform: scaleX(0);
    transition: 0.5s ease;
  }
  section{
    font-size: 14px;
    width: 90%;
  }
  &:hover .item_content_listmenu_price svg#Layer_eye .cls-1{
    fill: #ffff3e;
  }
}

/* swiper bottom scrollbar full */
.slide_listmenu.swiper-container {
  height:100%;
  margin: 0 auto;
  overflow: hidden;
  position: absolute;
  top: 0;
  transition: all 0.3s ease-in-out 0.7s;
  -webkit-transition: all 0.3s ease-in-out 0.7s;
  -moz-transition: all 0.3s ease-in-out 0.7s;
  -ms-transition: all 0.3s ease-in-out 0.7s;
  -o-transition: all 0.3s ease-in-out 0.7s;
  width: 100%;
}
.slide_listmenu .swiper-scrollbar { 
  left: 0; opacity: 1;
  position: absolute;
  width: 100%;
  bottom:0;
  border-radius: 0;
  height: 2px; 
}
.slide_listmenu .swiper-scrollbar-drag {
  border-radius: 0;
  background: #ff214f;
}
.slide_listmenu .swiper-scrollbar {
  background: rgba(255,255,255,.2);
}
.slide_listmenu .swiper-slide .content_listmenu{
