.img_product{
  width: 50%;
  > .this_img_product{
    width: 100%;
    position: relative;
     >section{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(255,255,62,1);
      transform-origin: 0% 0%;
      transform: scaleX(0); 
     }
  }
    ul{
      position: relative;
      width: 100%;
      overflow: hidden;
      li{
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        &:nth-child(1){
          position: relative;
          opacity: 1;
        }
      }
    }
}
.button_slide_product{
  section{
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    width: 50px;
    cursor: pointer;
    &:nth-child(1){
      left: 5%;
    }
    &:nth-child(2){
      right: 5%;
    }
  }
}

.note_product{
  width: 70%;
  color: #ffffff;
  ul{
    position: relative;
    width: 100%;
    li{
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      visibility: hidden;
      article{
        font-size: 40px;
        color: rgba(255,255,62,1);
        margin-bottom: 20px;
        opacity: 0;
        visibility: hidden;
      }
      section{
        margin-bottom: 30px;
        opacity: 0;
        visibility: hidden;
      }
      a{
        font-size: 25px;
        color: #ffffff;
        position: relative;
        display: inline-block;
        padding-right: 50px;
        text-decoration: none;
        opacity: 0;
        visibility: hidden;
        &::before{
          content: "";
          position: absolute;
          top: 50%;
          left: 0;
          transform-origin: 0% 0%;
          width: 100%;
          height: 1px;
          transform: scaleX(0);
          background: #ffffff;
          transition: 0.5s ease;
        }
        &:hover::before{
          transform: scaleX(1);
        }
      }
      &:nth-child(1){
        position: relative;
        visibility: visible;
          article, section, a{
            opacity: 1;
            visibility: visible;
          }
        }
      }
        
  }
}


@media (max-width: 768px){
  .slide_img_product{
    height: 50vh;
  }
  .slide_note_product{
    height: 50vh;
    display: block;
    justify-content: none;
    align-items: none;
    font-size: 12px;
  }
  .slide_note_product > h2{
    top: 20%;
    width: 100%;
    font-size: 90px;
    color: #292929;
    font-weight: bold;
    z-index: 1;
  }
  .note_product{
    position: relative;
    width: 100%;
    padding: 10px 20px;
    z-index: 2;
     ul li{
       article{
         margin-bottom: 5px;
       }
       section{
         margin-bottom: 10px;
       }
     }
  }
}

@media (max-width: 320px) and (max-height: 568px){
  .slide_img_product{
    height: 40vh;
  }
  .slide_note_product{
    height: 60vh;
  }
  .note_product{
     ul li{
       article{
         font-size: 30px;
         margin-bottom: 0;
       }
       section{
         margin-bottom: 5px;
       }
       a{
         font-size: 20px;
       }
      }
  }
}

/* end product */





/* start content product */


.content_product{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.title_content_product{
  color: #ffffff;
  font-size: 40px;
  position: fixed;
  top: 2%;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 20;
}
.this_content_product{
  width: 25%;
  position: relative;
    .img_content_product{
      width: 100%;
      transform-origin: 0% 0%;
    }
    .svg_content_product{
      section{
        width: 300px;
        position: absolute;
        svg{
          overflow: visible;
          .ball_content_product{
            fill: #000000;
            stroke: #ffffff;
            stroke-width: 3px;
            r:  5;
          }
          .text01{
            fill: #ffffff;
            font-size: 30px;
             a{
               fill: #ffff3e;
             }
          }
        }
          &:nth-child(1){
            top: -5%;
            right: 90%;
          }
          &:nth-child(2){
            top: 40%;
            right: 90%;
          }
          &:nth-child(3){
            bottom: 0%;
            right: 90%;
          }
          &:nth-child(4){
            top: -10%;
            left: 90%;
          }
          &:nth-child(5){
            top: 35%;
            left: 90%;
          }
          &:nth-child(6){
            bottom: -10%;
            left: 90%;
          }
        }
    }
}
.mobile_content_product{
  opacity: 0;
  visibility: hidden;
  width: 0;
  height: 0;
}

@media (max-width: 1024px){
  .this_content_product{
    width: 20%;
    .svg_content_product{
      section{
        width: 220px;
      }
    }
  }
}

@media (max-width: 768px){
  .this_content_product{
    width: 30%;
    .svg_content_product{
      section{
        width: 180px;
         svg .text01{
          font-size: 25px;
        }
      }
    }
  }
}

@media (max-width: 600px){
  .content_product{
    display: block;
    justify-content: none;
  }
  .title_content_product{
    font-size: 30px;
  }
  .this_content_product{
    width: 50%;
    margin: auto;
    margin-top: 25%;
  }
  .svg_content_product{
    opacity: 0;
    visibility: hidden;
  }
  .mobile_content_product{
    opacity: 1;
    visibility: visible;
    color: #ffffff;
    font-size: 17px;
    margin-top: 50px;
    width: 100%;
    height: "auto";
     ul li{
       margin-bottom: 10px;
     }
  }
}

@media (max-width: 320px) and (max-height: 568px){
  .this_content_product{
    width: 50%;
    margin: auto;
    margin-top: 20%;
  }
  .mobile_content_product{
    font-size: 15px;
    margin-top: 40px;
     ul li{
       margin-bottom: 5px;
     }
  }
}